/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
    background-image: url("../images/bg.gif");
    background-repeat: repeat;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

/* main css */
/* back-to-top button */

.container {
    position: relative;
    width: 85%;
    margin-left: auto;
    margin-right: auto;
    overflow-x:hidden;
   /*  height: 100vh; */
}

.back {
    display: none;
    position: fixed;
    right: 130px;
    bottom: 80px;
    width: 70px;
    padding: 3px;
    border: none;
    border-radius: 4px;
    opacity: 0.4;
    cursor: pointer;
    background-color: #980000;
    color:#fffde8;
    text-align: center;
    line-height: 1.5;
    z-index: 200;
}

.back:hover {
    background: #993636;
}

.intro {
    width: 100%;
    background-color: #fff;
}

.intro-img {
    display: block;
    width: 90%;
    height: 110px;
    margin: 0 auto;
}

.header {
    width: 100%;
}

.header-nav {
    background-image: url("../images/menu-bg.jpg");
    background-repeat: repeat;
    width: 100%;
    height: 60px;
}

.menu-list {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.menu-item {
    display: flex;
    align-items: center;
    font-size: 22px;
}

.menu-item a {
    color: #fff;
    text-decoration: none;
}

.menu-item a:hover {
    color: #c8c5c5;
}

.intro-image {
    width: 100%;
}

.intro-image img {
    display: block;
    width: 100%;
    height: 250px;
}

/* footer */
.footer {
    background-image: url('../images/stopka.jpg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: 100%;
    height: 45px;
}

.footer-info {
    text-align: end;
    color: #aaa;
    font-size: 14px;
    padding: 15px;
}

/* links section */
.links {
    width: 100%;
    background-color: #fffde8;
}

.links-headline {
    font-size: 26px;
    text-align: center;
    padding: 30px 0;
    color: #323280;
    letter-spacing: 2px;
}

.links-box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0 0 30px;
}

.inner-box {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.inner-box p {
    font-size: 18px;
    padding: 10px 0;
}

.inner-box p a {
    text-decoration: none;
    font-size: 22px;
}

.inner-box p a:hover {
    text-decoration: underline;
}

/* media query */
@media only screen and (max-width: 1160px) {
    .container {
        width: 100%;
    }
}

@media only screen and (max-width: 1100px) {
    .header-nav {
        width: 100%;
        height: 210px;
        background-image: none;
        background-image: linear-gradient(to top right, #db0505, #750000);
    }

    .hr-shadow {
        border: 1px solid rgb(255, 253, 232);
        box-shadow: 0px -1px 9px 4px rgba(255, 253, 232, 0.7);
        margin: 0;
    }

    .menu-list {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
    }

    .menu-item {
        font-size: 16px;
        letter-spacing: 3px;
        font-weight: bolder;
    }

    .intro-image img {
        height: 180px;
    }

    .menu-list img {
        display: none;
    }
}

@media only screen and (max-width: 720px) {
    .intro-img {
        height: 150px;
    }
}

@media only screen and (min-width: 530px) {
    .intro-alt {
        display: none;
    }
}

@media only screen and (min-width: 1101px) {
    .hr-shadow {
        display: none;
    }
}

@media only screen and (max-width: 530px) {

    .links-box {
        padding: 0 20px 20px;
    }

    .menu-item {
        font-size: 12px;
    }

    .header-nav {
        height: 160px;
    }

    .studio-link {
        font-size: 16px;
    }
}
